.productDiv{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.partDivs{
    display: flex;
    flex-wrap: wrap;
    flex-basis: 50%;
    min-width: 400px;
    justify-content: space-between;
    align-items: center;
}
.content{
    display: flex;
    width: 33%;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 100px;
}