.productPage{
    display: flex;
    background-color: rgb(210, 244, 233);
    max-width: 100vw;
    border: 2px solid black;
    height: 85vh;
}

.imagesContainer{
    width: 40%;
    height: 85vh;
    display: flex;
    align-items: center;
    margin: 1rem;
}
.imagesContainer img{
    width: 100%;
    height: 70vh;
    border-radius: 0%;
}

.featuresNdActions{
    width: 60%;
    height: 85vh;
    

}
.productDetailsContainer{
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.cartDetails{
    height: 30vh;
}

