.checkoutPage {
    height: 90vh;
    display: flex;
}

.cartSummary {
    border: 2px solid rgb(19, 19, 19);
    height: 100%;
    width: 45%;
}

.itemsDisplay {
    height: 72%;
    max-height: 72%;
}

.checkoutDiv {
    height: 100%;
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    
}

.paymentInterfaceDiv {
    border: 0px solid red;
}

.paybtn {
    background-color: cornflowerblue;
}

.shipAddress {
   border: 1px solid black;
    height: 50%;
    width: 100%;
}

.shipAddress button {
    margin: 2rem;
}

.shipHeader,
.shipFooter {
    height: 15%;
}

.shipDisplay {
    height: 70%;
}

.pItem {
    display: flex;
    border: 1px solid black;
}

.pItem2 {
    display: flex;
    justify-content: flex-start;
}

.pItem3 {
    display: flex;
    justify-content: flex-end;
}

.pItem3 div {
    width: 20%;
}


.pDesc {
    display: flex;
    min-width: 45%;
    justify-content: space-between;
    border-right: 1px solid black;

}

.pQuant {
    width: 15%;
    border-right: 1px solid black;

}

.pPrice,
.pAmount {
    width: 20%;
    border-right: 1px solid black;

}

.billDisplay {
    background-color: rgb(240, 227, 184);
    height: 22%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.itDis {
    overflow-y: auto;
    max-height: calc(82vh - 2px);
    /* Subtracting 2px to account for the border */
    border: 1px solid red;
}

@media screen and (max-width:900px) {
    .checkoutPage {
        min-height: 90vh;
        height: auto;
        display: flex;
       
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .cartSummary {
        border: 2px solid rgb(19, 19, 19);
        height: 100%;
        min-width: 400px;
        width: 100%;
    }

    .itemsDisplay {
        height: 72%;
        max-height: 72%;
    }

    .checkoutDiv {
        height: 100%;
        min-width: 400px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        
    }

    .shipAddress {
       
        height: 50%;
        width: 100%;
    }
}