.productCard{
    margin: 1rem;
    background-color: rgb(222, 202, 177);
    box-shadow: 2px 2px 2px 2px rgb(230, 197, 157);
    min-width: 25%;
    width: 100%;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* flex-wrap: wrap; */
    text-align: center;
    box-sizing: border-box;

    word-wrap: break-word;

}
.productDetail{
    padding: 0.5rem;
    font-size: larger;
    color: rgb(42, 30, 13);
    max-width: 30vw;
    min-width: 20vw;
    box-sizing: border-box;
    word-wrap: break-word;
}

.productDetail img{
    max-width: 25vw;
    width: 22vw;
    /* height: 40vh; */
    max-height: 50vh;
    margin-right: 1rem;
}

.namepd{
    width: 15vw;
}
.descpd{
    width: 20vw;
}

