.registerPage{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: bisque;
    background-color: rgba(152, 67, 231, 0.921);
}
.formContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
}

.signupForm{
    display: flex;
    align-items:center;
    background-color: blueviolet;
    width: 40%;
    min-width: 350px;
    flex-wrap: wrap;
    font-size: 1.2rem;
    justify-content: center;
    box-shadow: 4px 4px 4px 4px rgb(118, 17, 212);
}
.formsComponent{
    width: 100%;

    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
}

.formsComponent label,input,select{
    min-width: 70%;
    border-radius: 4%;
    margin-top:0.1rem ;
    display: block;
}

input,select{
    background-color: rgb(225, 170, 248);
    padding: 0.4rem;
    min-width: 70%;
}
input[type='submit']{
    background-color: rgb(47, 138, 242);
    color: rgb(238, 213, 229);
    font-size: larger;
    text-shadow: 1px 1px black;
    font-weight: 800;
    margin: 1rem;
}

button{
    color: antiquewhite;
    background-color: blueviolet;
    font-size: large;
}


